.typography {
  color: #0d2344;
}

.typography[data-font="primary"] {
  font-family: var(--font-unbounded, "Unbounded", sans-serif);
}

.typography[data-font="secondary"] {
  font-family: "Gothic", "Century Gothic", "CenturyGothic";
}

.typography[data-weight="extraBold"] {
  font-weight: 800;
}

.typography[data-weight="medium"] {
  font-weight: 500;
}

.typography[data-weight="regular"] {
  font-weight: 400;
}

.typography[data-weight="light"] {
  font-weight: 300;
}